import { Canvas,extend, useLoader, useFrame, useThree,} from '@react-three/fiber'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import { useSpring, useTransition, animated,config } from '@react-spring/three'
import React, { Suspense, useEffect, useRef, useState, useMemo } from 'react'
import * as THREE from 'three'
import { OrbitControls, Sky, Cloud, useHelper, Preload  } from '@react-three/drei'
import { Geometry,Water } from "three-stdlib";
import Wave from 'react-wavify'

import Make_spotlight from './spot_light'



function add_2_lists(lista,listb){
  return [lista[0]+listb[0],lista[1]+listb[1],lista[2]+listb[2]]
}
function hextorgb(hex) {
  var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
  return result ? {
    r: parseInt(result[1], 16),
    g: parseInt(result[2], 16),
    b: parseInt(result[3], 16)
  } : null;
}
function componentToHex(c) {
  var hex = c.toString(16);
  return hex.length == 1 ? "0" + hex : hex;
}
function rgbtohex(r, g, b) {
  return "#" + componentToHex(r) + componentToHex(g) + componentToHex(b);
}
const Island = () => {
  var gltf = useLoader(GLTFLoader, './island/Redoneisland.glb')
  const material = new THREE.MeshPhongMaterial({
    color: '#C2B280',
    flatShading: true,
  });

  return(
    <mesh
    shadows
    castShadow
    receiveShadow
    geometry={gltf.nodes.Landscape001.geometry}
    scale={200}
    material={material}
    position={[0,-11,-250]}
     >
      </mesh>
  )
}
const Zeppelin = props => {
  var gltf = useLoader(GLTFLoader, './zeppelin/scene.gltf')
  var textur = useLoader(GLTFLoader, './zeppelin/texture/scene.gltf')
  const zep = gltf.nodes.mesh_0.geometry
  const rota = 3.14*2;
  const styles = useSpring({
    loop: false,
    from: { rotation: [0, 3.14, 0 ],
             position: add_2_lists(props.position,[0,0,0]),
           },
    to: { rotation: [0, 3.14/2, 0],
          position: add_2_lists(props.position,[0,-5,0])
          },
    config:{ mass: 200, tension: 100, friction: 0 }
  });

  // console.log(gltf)

  const [hovered, sethovered] = useState(false)
  const [starter, setsstarter] = useState(false)
  const [colorfrom, setcolorfrom] = useState('#000000')
  const [counter, setcounter] = useState()
  const zep_col_dark = '#012a4a'
  const zep_col_light = '#014f86'
  const [colorto, setto] = useState('#000000')
  useEffect(() => {

    let intervalId;
    let perc = 100;

    if((hovered)&&(starter)){
      setcounter(perc)
      setto(zep_col_dark)
      setcolorfrom(zep_col_light)
    }
    if (hovered && (starter==false)){
      setsstarter(true)
      setcounter(0)
      setto(zep_col_dark)
      setcolorfrom(zep_col_light)
    }
    if(starter){
      intervalId = setInterval(() => {
        setcounter(counter + 1);
      }, 1)
    }
    if (counter < perc){
      var r = THREE.MathUtils.lerp(parseInt(hextorgb(colorfrom).r),parseInt(hextorgb(colorto).r),counter/perc)
      var g = THREE.MathUtils.lerp(parseInt(hextorgb(colorfrom).g),parseInt(hextorgb(colorto).g),counter/perc)
      var b = THREE.MathUtils.lerp(parseInt(hextorgb(colorfrom).b),parseInt(hextorgb(colorto).b),counter/perc)
      var c = rgbtohex(parseInt(r),parseInt(g),parseInt(b))
      setcolorfrom(c)
    }else{
      setsstarter(false)
    }
   document.body.style.cursor = hovered ? "pointer" : "auto"
   return () => clearInterval(intervalId);
 }, [hovered,counter])
const material = new THREE.MeshPhongMaterial({
  color: colorfrom,    // red (can also use a CSS color string here)
  flatShading: true,
  transmission:1
});
  return(
    <>
    <animated.mesh shadowMap
    castShadow
    receiveShadow { ...styles}>
          <mesh castShadow receiveShadow shadowMap
          geometry={gltf.nodes.mesh_0.geometry}
           scale={2}
           onPointerOver={(e) => (e.stopPropagation(), sethovered(true))
         }
          onPointerOut={(e) => (e.stopPropagation(), sethovered(false))}
          material={material}
           >
            </mesh>
      </animated.mesh>
    </>
  );

}

extend({ Water })
function Ocean() {
  const ref = useRef()
  const gl = useThree((state) => state.gl)
  const waterNormals = useLoader(THREE.TextureLoader, '/0110.png')
  waterNormals.wrapS = waterNormals.wrapT = THREE.RepeatWrapping
  const geom = useMemo(() => new THREE.PlaneGeometry(20000, 20000), [])
  const config = useMemo(
    () => ({
      textureWidth: 512,
      textureHeight: 512,
      waterNormals,
      sunDirection: new THREE.Vector3(-1),
      sunColor: 'hotpink',
      waterColor: '#012a4a',
      distortionScale: 3,

      fog: gl.fog !== undefined,
      format: gl.encoding
    }),
    [waterNormals]
  )
  useFrame((state, delta) => (ref.current.material.uniforms.time.value += delta))


  const styles = useSpring({
    loop: false,
    from: {
             position: [0,0.5,0],
           },
    to: {
          position: [0,-.5,0]
          },
    config:{ mass: 10, tension: 10, friction: 0 }
  });


  return (

    <animated.mesh {...styles}>
      <water ref={ref} args={[geom, config]} rotation-x={-Math.PI / 2} />
    </animated.mesh>

  )
}
function Dolly({cam_pos=[0,0,0]}) {
  const strength =.008
  let unit_vec = new THREE.Vector3(0 - cam_pos[0],0,-250 - cam_pos[2])
  unit_vec = unit_vec.normalize()
  const x_offset = unit_vec.x*100
  const z_offset = unit_vec.z*100

  useFrame(({camera}) => {
    camera.position.x += ((cam_pos[0]-x_offset)-camera.position.x)*strength
    camera.position.y += (cam_pos[1]-camera.position.y)*strength
    camera.position.z += ((cam_pos[2]-z_offset)-camera.position.z)*strength
  })
  return null
}

function My_text({text}){

  return(
    <div>
    <h2 style={{position:'relative', top: '50px', left:'40%', color:'#C2B280'}}>
      {text}
    </h2>
    </div>
  )
}


